EACE;IACE;;;uBAGmB;EACrB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,kBAAkB;IAClB,UAAU;IACV,aAAa;IACb,WAAW;IACX,SAAS;IACT,gBAAgB;IAChB,WAAW;EACb;;EAEA;IACE,aAAa;IACb,kBAAkB;IAClB,WAAW;IACX,QAAQ;IACR,eAAe;EACjB;;EAEA;IACE,YAAY;IACZ;EACF;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,cAAc;IACd,iBAAiB;IACjB,0BAA0B;IAC1B,eAAe;EACjB;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,iBAAiB;IACjB,UAAU;EACZ;;EAEA;IACE,iBAAiB;EACnB;AACF","sourcesContent":["@font-face {\n  font-family: 'Mathlete';\n  src: url('../assets/Mathlete-Bulky-webfont.woff') format('woff'),\n    url('../assets/Mathlete-BulkySlant-webfont.woff') format('woff'),\n    url('../assets/Mathlete-Skinny-webfont.woff') format('woff'),\n    url('../assets/Mathlete-SkinnySlant-webfont.woff') format('woff');\n  font-weight: normal;\n  font-style: normal;\n}\n\n:root {\n  --main-color: #3AAFA9;\n  --secondary-color: #2B7A78;\n  --font-color: #DEF2F1;\n  --border-color: #17252A;\n  --border-radius: 15px;\n  --green-color: lawngreen;\n}\n\n*,\n*::before,\n*::after {\n  font-size: 1.2rem;\n  font-family: inherit;\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n  font-family: 'Mathlete', 'sans-serif';\n  background-color: var(--main-color);\n  color: var(--font-color);\n  display: grid;\n  grid: \"header header header header\" auto \n        \"projects tasks tasks tasks \" 76vh\n        \"footer footer footer footer\" 50px/ \n        350px minmax(250px, auto) 1fr 1fr;\n}\n\nbody h1 {\n  grid-area: header;\n  background-color: var(--secondary-color);\n  text-align: center;\n  padding: 0.2em;\n  margin: 0.25em;\n  font-size: 4.5rem;\n  border: 3px solid var(--border-color);\n  border-radius: var(--border-radius);\n}\n\n/* project list  */\n\n.all-projects {\n  grid-area: projects;\n  background-color: var(--secondary-color);\n  border: 3px solid var(--border-color);\n  border-radius: var(--border-radius);\n  margin: 0.4em 0.6em 0.4em 1em;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n}\n\n.close-list { \n  display: none;\n}\n\n.project-list-title {\n  text-align: center;\n  padding: 0.8rem;\n  margin: 0;\n  font-size: 2.5rem;\n  width: 90%;\n  border-bottom: 2px solid var(--border-color);\n}\n\n.project-list {\n  font-weight: 400;\n  font-style: italic;\n  width: 90%;\n  line-height: 1.5;\n  padding: 0.4rem;\n  margin: 0;\n  text-align: center;\n}\n\n.project-name {\n  list-style: none;\n  width: 100%;\n  font-size: 2rem;\n  margin-bottom: 0.2rem;\n  cursor: pointer;\n  border-radius: 5px;\n  transition: background-color 200ms ease-in-out;\n}\n\n.project-name:hover {\n  background-color: var(--main-color);\n  font-weight: 700;\n}\n\n.active-project {\n  font-weight: 700;\n  background-color: var(--main-color);\n  opacity: 0.8;\n}\n\n.btn {\n  cursor: pointer;\n  background: 0;\n  border: 0;\n  padding: 0;\n  color: inherit;\n  transition: background-color 200ms ease-in-out;\n}\n\n.btn:hover {\n  background-color: var(--main-color);\n}\n\n.btn.open-add-project {\n  font-size: 2rem;\n  font-weight: 700;\n  margin: 0.5rem;\n  margin-top: 0;\n  padding: 0.3rem 0;\n  width: 85%;\n  border-radius: 5px;\n}\n\n.btn.open-add-project:hover {\n  color: var(--green-color);\n}\n\n/* task list  */\n\n.todo-list {\n  grid-area: tasks;\n  background-color: var(--secondary-color);\n  border: 3px solid var(--border-color);\n  border-radius: var(--border-radius);\n  margin: 0.4em 1em 0.4em 0.6em;\n  padding: 0.8rem;\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n}\n\n.todo-header {\n  width: 100%;\n  border-bottom: 2px solid var(--border-color);\n  padding: 0.3rem;\n}\n\n.todo-header-bar {\n  display: flex;\n  justify-content: space-between;\n}\n\n.todo-header-bar h2 {\n  font-size: 4rem;\n  font-weight: 700;\n  font-style: italic;\n  margin: 0;\n}\n\n.todo-edit-remaining p{\n  margin: 0;\n  padding: 0.1rem;\n  text-align: end;\n}\n\n.show-project-list {\n  display: none;\n}\n\n#project-name-edit { \n  font-size: 3.8rem;\n  padding-left: 0.5rem;\n}\n\n#project-description-edit {\n  font-size: 2rem;\n  padding-left: 0.5rem;\n  width: 100%;\n}\n\n.edit-project {\n  width: 7rem;\n  height: 3rem;\n  font-size: 2rem;\n  border: 2px solid var(--main-color);\n}\n\n.todo-edit-delete {\n  display:flex;\n  justify-content: space-between;\n  padding-left: 1rem;\n}\n\n.todo-edit-delete p{\n  font-size: 1.6rem;\n  text-decoration: underline;\n  cursor: pointer;\n}\n\n.todo-edit-delete p:hover {\n  opacity: 0.7;\n}\n\n.todo-header h4 {\n  font-size: 1.5rem;\n  font-weight: 400;\n  margin: 0;\n}\n\n.todo-body {\n  padding: 0.5rem;\n  width: 100%;\n}\n\n[type=\"checkbox\"] {\n  opacity: 0;\n  position: absolute;\n}\n\n.custom-checkbox {\n  --size: 1rem;\n  display: inline-block;\n  width: var(--size);\n  height: var(--size);\n  cursor: pointer;\n  border: 2px solid var(--border-color);\n  transform: scale(1);\n  transition: transform 150ms ease-in-out;\n}\n\n.task:hover .custom-checkbox,\n[type=\"checkbox\"]:focus + label .custom-checkbox {\n  transform: scale(1.15);\n}\n\n[type=\"checkbox\"]:checked + label .custom-checkbox{\n  background: var(--green-color);\n  box-shadow: inset 0 0 0px 1px var(--secondary-color);\n}\n\n[type=\"checkbox\"]:checked + label {\n  opacity: 0.5;\n}\n\n.task {\n  display: flex;\n  justify-content: space-between;\n  font-size: 2.4rem;\n  padding: 0.5rem;\n  border-radius: 5px;\n}\n\n.task:hover {\n  background-color: var(--main-color);\n}\n\n.task label {\n  position: relative;\n  font-size: 1.6rem;\n  cursor: pointer;\n}\n\n.task label::after {\n  content: \"\";\n  position: absolute;\n  left: 1.3rem;\n  right: -0.2rem;\n  top: 40%;\n  height: 3px;\n  background-color: var(--border-color);\n  transform: scaleX(0);\n  transform-origin: right;\n  transition: transform 